// Lager modul for å holde oversikt over varer
// Viser beholdning av impregnert staur, granstaur, skier, ståltråd
'use client'

import { useEffect, useState } from 'react'
import { fetchLager, updateLager, fetchLagerHistorikk } from '@/lib/supabase'

// Typer for lager og historikk
interface LagerHistorikk {
  created_at: string;
  navn: string;
  type: string;
  antall: number;
  kommentar: string;
}

interface LagerForm {
  [key: string]: number;
}

const initialItems = [
  { name: 'Impregnert staur', key: 'impregnert_staur' },
  { name: 'Granstaur', key: 'granstaur' },
  { name: 'Skier', key: 'skier' },
  { name: 'Ståltråd', key: 'staltrad' }
]

export default function LagerPage() {
  const [lager, setLager] = useState<LagerForm>({})
  const [loading, setLoading] = useState(true)
  const [edit, setEdit] = useState(false)
  const [form, setForm] = useState<LagerForm>({})
  const [historikk, setHistorikk] = useState<LagerHistorikk[]>([])

  useEffect(() => {
    const getLager = async () => {
      setLoading(true)
      const data = await fetchLager()
      setLager(data || {})
      setForm(data || {})
      setLoading(false)
    }
    const getHistorikk = async () => {
      const data = await fetchLagerHistorikk()
      setHistorikk(data || [])
    }
    getLager()
    getHistorikk()
  }, [])

  const handleChange = (key: string, value: string) => {
    setForm(f => ({ ...f, [key]: Number(value) }))
  }

  const handleSave = async () => {
    await updateLager(form)
    setLager(form)
    setEdit(false)
  }

  return (
    <div className="min-h-screen bg-gray-900 p-4 md:p-8 ml-0 md:ml-64">
      <div className="max-w-2xl mx-auto bg-gray-800 rounded-2xl p-6 border border-gray-700">
        <h1 className="text-2xl font-bold text-white mb-6">Lageroversikt</h1>
        {loading ? (
          <div className="text-gray-400">Laster...</div>
        ) : (
          <table className="w-full text-gray-300">
            <thead>
              <tr>
                <th className="text-left pb-2">Vare</th>
                <th className="text-left pb-2">Antall</th>
              </tr>
            </thead>
            <tbody>
              {initialItems.map(item => (
                <tr key={item.key} className="border-t border-gray-700">
                  <td className="py-2">{item.name}</td>
                  <td className="py-2">
                    {edit ? (
                      <input
                        type="number"
                        className="bg-gray-700 rounded px-2 py-1 w-24 text-white"
                        value={form[item.key] || ''}
                        onChange={e => handleChange(item.key, e.target.value)}
                      />
                    ) : (
                      lager[item.key] ?? 0
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        <div className="mt-6 flex gap-3">
          {edit ? (
            <>
              <button className="bg-blue-600 px-4 py-2 rounded text-white" onClick={handleSave}>Lagre</button>
              <button className="bg-gray-600 px-4 py-2 rounded text-white" onClick={() => setEdit(false)}>Avbryt</button>
            </>
          ) : (
            <button className="bg-blue-600 px-4 py-2 rounded text-white" onClick={() => setEdit(true)}>Rediger</button>
          )}
        </div>
        <div className="mt-10">
          <h2 className="text-xl font-semibold text-white mb-4">Historikk</h2>
          <div className="overflow-x-auto">
            <table className="w-full text-gray-300">
              <thead>
                <tr>
                  <th className="text-left pb-2">Tidspunkt</th>
                  <th className="text-left pb-2">Vare</th>
                  <th className="text-left pb-2">Type</th>
                  <th className="text-left pb-2">Antall</th>
                  <th className="text-left pb-2">Kommentar</th>
                </tr>
              </thead>
              <tbody>
                {historikk.length === 0 ? (
                  <tr><td colSpan={5} className="py-2 text-gray-500">Ingen historikk funnet</td></tr>
                ) : (
                  historikk.map((h, idx) => (
                    <tr key={idx} className="border-t border-gray-700">
                      <td className="py-2">{new Date(h.created_at).toLocaleString('no-NO')}</td>
                      <td className="py-2">{h.navn}</td>
                      <td className="py-2">{h.type}</td>
                      <td className="py-2">{h.antall}</td>
                      <td className="py-2">{h.kommentar}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  )
}
